@import url(https://fonts.googleapis.com/css?family=Libre+Franklin&display=swap);
html, body, #root {
  min-height: 100vh;
  background-color: #0C0C0C; }

*::-moz-selection {
  background: #F1284C; }

*::selection {
  background: #F1284C; }

body {
  margin: 0;
  overflow-x: hidden; }

html {
  font-size: calc(100% * 0.625); }

body {
  font-size: 1.6rem;
  line-height: 1.8;
  color: #eee;
  font-family: "Libre Franklin", sans-serif; }

button, input {
  font-family: "Libre Franklin", sans-serif;
  font-size: 1.6rem; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1;
  color: #fff;
  font-family: "Gilroy"; }

@font-face {
  font-family: 'Gilroy';
  font-weight: bold;
  src: url("/fonts/Gilroy-ExtraBold.otf"); }

.app {
  width: 100%;
  min-height: 100vh; }

.button {
  padding: 1.5rem 3rem;
  border-radius: 3rem;
  background-color: #E8294C;
  border-color: #E8294C;
  font-weight: bold;
  color: #eee;
  border: 0;
  cursor: pointer;
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s; }
  .button:hover {
    background-color: #c81636; }
  .button__secondary {
    background-color: #8B0171;
    border-color: #8B0171; }
    .button__secondary:hover {
      background-color: #580148; }
  .button__outline {
    border: 0.1rem solid;
    background-color: transparent; }

.section {
  width: 100%;
  min-height: 40rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 20rem;
  position: relative; }
  .section--glown {
    background-image: -webkit-radial-gradient(50% -100%, circle, rgba(72, 162, 245, 0.7), rgba(38, 91, 139, 0) 70%);
    background-image: radial-gradient(circle at 50% -100%, rgba(72, 162, 245, 0.7), rgba(38, 91, 139, 0) 70%); }
  .section__layout {
    width: 95rem;
    flex: 1 1; }
  .section__line {
    bottom: 0;
    position: absolute;
    z-index: 0; }

.line {
  width: 100%;
  pointer-events: none; }

.heading-section {
  padding-top: 5rem;
  display: flex;
  flex-direction: column; }
  .heading-section__logo {
    align-self: flex-start; }
  .heading-section__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 60rem;
    margin-top: 5rem; }
  .heading-section__action {
    margin-top: 1rem; }

.section-info {
  position: relative; }
  .section-info--rtl {
    text-align: right;
    align-items: flex-end; }
  .section-info__glow {
    position: absolute;
    -webkit-transform: translate(-30%, -30%);
            transform: translate(-30%, -30%);
    pointer-events: none; }
    .section-info__glow--rtl {
      -webkit-transform: translate(30%, 30%);
              transform: translate(30%, 30%); }
  .section-info__title {
    font-size: 7.6rem;
    display: flex;
    flex-direction: column;
    margin: 1rem 0; }
  .section-info__desc {
    display: flex;
    flex-direction: column; }

